.hero {
  --hero-background-image: none;
  --hero-background-color: var(--color-primary, $primary-color);
  --hero-heading-color: var(--color-white, $white);
  --hero-description-color: var(--color-white, $white);

  background-image: var(--hero-background-image);
  background-color: var(--hero-background-color, $primary-color);
  color: var(--hero-heading-color, $white);
  min-height: 21.5rem;
  padding: 21.5rem 0 3rem;
  background-position: top right;
  background-repeat: no-repeat;
  background-size: 48rem;

  @include for-tablet-up {
    min-height: 18.75rem;
    padding-top: 3rem;
    background-size: cover;
  }

  &__content {
    display: flex;
    gap: 2rem;
    flex-direction: column;

    @include for-tablet-up {
      width: 50%;
    }
  }

  &__heading {
    font-family: $heading-font;
    color: var(--hero-heading-color, $white);
  }

  &__description {
    flex-wrap: pretty;
    color: var(--hero-description-color, $white);
  }

  &__calls_to_action {
    display: flex;
  }
}