.hero {
  --hero-background-image: none;
  --hero-background-color: var(--color-primary, $primary-color);
  --hero-heading-color: var(--color-white, $white);
  --hero-description-color: var(--color-white, $white);
  background-image: var(--hero-background-image);
  background-color: var(--hero-background-color, #134740);
  color: var(--hero-heading-color, #FFFFFF);
  min-height: 21.5rem;
  padding: 21.5rem 0 3rem;
  background-position: top right;
  background-repeat: no-repeat;
  background-size: 48rem;
}
@media (min-width: 48rem) {
  .hero {
    min-height: 18.75rem;
    padding-top: 3rem;
    background-size: cover;
  }
}
.hero__content {
  display: flex;
  gap: 2rem;
  flex-direction: column;
}
@media (min-width: 48rem) {
  .hero__content {
    width: 50%;
  }
}
.hero__heading {
  font-family: "Roboto Serif", sans-serif;
  color: var(--hero-heading-color, #FFFFFF);
}
.hero__description {
  flex-wrap: pretty;
  color: var(--hero-description-color, #FFFFFF);
}
.hero__calls_to_action {
  display: flex;
}

.icon_grid {
  background-color: var(--icon-grid-background-color, #FFFFFF);
  padding: 3.125rem 0;
  display: flex;
  flex-direction: column;
  font-size: 1.125rem;
  line-height: 1.35rem;
}
.icon_grid__content {
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.icon_grid__super_title {
  color: var(--icon-grid-super-title-color, #EC6B48);
}
.icon_grid__title {
  color: var(--icon-grid-title-color, #134740);
  padding: 0 3.125rem;
}
.icon_grid__subtitle {
  color: var(--icon-grid-sub-title-color, #000000);
  font-weight: 400;
  font-size: 1.125rem;
}
.icon_grid__items {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  padding: 0 3.125rem;
}
@media (min-width: 48rem) {
  .icon_grid__items {
    grid-template-columns: repeat(4, 1fr);
    padding: 0;
  }
}
.icon_grid__item {
  border-bottom: 0.125rem solid var(--icon-grid-accent-color, #EC6B48);
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
  padding: 1.875rem 1.25rem;
  text-align: left;
  justify-content: start;
}
