    content: " ";
    position: absolute;
    width: 0.125rem;
    height: 100%;
    background-color: var(--icon-grid-accent-color, #EC6B48);
    top: 0;
    right: 0;
  }
}
.icon_grid__item_picture {
  width: 5.3125rem;
}
.icon_grid__item_call_to_action {
  color: #134740;
  font-weight: 600;
