.tabs__item_content {
  opacity: 0;
  position: absolute;
  transform: translateX(-10%);
  transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
  z-index: -1;
}
.tabs__item_content.active {
  opacity: 1;
  z-index: 1;
  transform: translateX(0);
  position: relative;
}

.stack_tabs {
  --stack-tabs-background-image: none;
  --stack-tabs-background-color: var(--color-primary);
  --stack-tabs-super-title-color: var(--color-secondary);
  --stack-tabs-title-color: var(--color-white);
  --stack-tabs-sub-title-color: var(--color-white);
  --stack-tabs-accent-color: var(--color-secondary);
  --stack-tabs-active-color: var(--color-terciary);
  background-image: var(--stack-tabs-background-image);
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  background-color: var(--stack-tabs-background-color);
  padding: 3.125rem 0;
}
.stack_tabs__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: var(--stack-tabs-title-color);
}
.stack_tabs__super_title {
  color: var(--stack-tabs-super-title-color);
}
.stack_tabs__super_title strong {
  color: var(--color-terciary);
}
.stack_tabs__title {
  color: var(--stack-tabs-title-color);
}
@media (min-width: 48rem) {
  .stack_tabs__title {
    width: 60%;
    margin: 1.125rem 0;
  }
}
.stack_tabs__subtitle {
  color: var(--stack-tabs-sub-title-color);
}
@media (min-width: 48rem) {
  .stack_tabs__subtitle {
    width: 60%;
    margin: 1.125rem 0;
  }
}
@media (min-width: 48rem) {
  .stack_tabs__container {
    display: flex;
  }
}
@media (min-width: 48rem) {
  .stack_tabs__images {
    width: 50%;
